.footer {
  padding: 6rem 0;
}
.footer__main {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6rem;
}
.footer__logo img {
  margin-bottom: 1rem;
}
.footer__logo span {
  margin-left: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 21px;
  letter-spacing: 3.94px;
}
.footer__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.footer__nav {
  display: flex;
  gap: 3rem;
}
.footer__social {
  display: flex;
  gap: 1rem;
}
.footer__copyright {
  display: flex;
  justify-content: space-between;
}
.footer__bottom-nav {
  display: flex;
  gap: 3rem;
}
.footer__bottom-nav a,
.footer__nav a {
  padding-bottom: 0.25rem;
  border-bottom: 2px solid var(--main-color-light);
}
.footer__bottom-nav a:hover,
.footer__nav a:hover {
  border-bottom: 2px solid var(--main-color-dark);
}
