.contact {
  background-color: var(--main-color-dark);
  padding: 6rem 0;
}
.contact .wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  gap: 5rem;
}
.contact__textbox {
  width: 80%;
  color: #fff;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
}
.contact__form {
  width: 80%;
  background-color: #fff;
  border-radius: 0.5rem;
  padding: 2rem;
}
.contact__form h3 {
  font-size: 1.5rem;
  font-family: var(--font-texts);
  margin-bottom: 1rem;
}
.contact__form form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.contact__form .contact__form-control input,
.contact__form .contact__form-control textarea {
  border: 2px solid lightgray;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  margin: 0.5rem 0;
  width: 100%;
}
.contact__form .contact__form-control textarea {
  height: 150px;
}
.contact__form-submit {
  text-align: right;
}
